import React from 'react';
import styled from 'styled-components';
import { Table, Space, Button, Popconfirm, message } from 'antd';

function ProjectTable({ dataSource, handleModify, handleDelete }: any) {
  function confirm(e: any, record: any) {
    console.log(record);
    handleDelete('project', record);
    // delete api .
    message.success('  ');
  }

  function cancel(e: any) {
    console.log(e);
    message.error('  ');
  }
  const columns = [
    {
      title: '',
      dataIndex: 'id',
      key: 'id',
    },
    {
      title: ' ',
      dataIndex: 'name',
      key: 'project'
    },
    {
      title: 'Scene ',
      dataIndex: '',
      key: ''
    },
    {
      title: 'Application Id',
      dataIndex: 'application_id',
      key: 'application_id',
    },
    {
      title: 'SMS ',
      dataIndex: 'sms_number1',
      key: 'sms_number1',
    },
    {
      title: '',
      dataIndex: 'sms_number1',
      key: 'sms_number2',
    },
    // {
    //   title: 'SMS ',
    //   dataIndex: 'sms',
    //   key: 'sms',
    //   render: (sms: string[], record: any) => (
    //     <>
    //       {sms.map(list => {
    //         return (
    //           <Tag color={'green'} key={list}>
    //             {list.toUpperCase()}
    //           </Tag>
    //         );
    //       })}
    //     </>
    //   ),
    // },
    {
      title: '',
      dataIndex: 'created_at',
      key: 'created_at',
    },
    {
      title: 'URL',
      dataIndex: 'url',
      key: 'url',
    },
    {
      title: ' ID',
      dataIndex: 'user_id',
      key: 'user_id',
    },
    {
      title: '',
      dataIndex: 'note',
      key: 'note',
      render: (text: string) => {
        return <NoteText>{text}</NoteText>;
      }
    },
    {
      title: 'Action',
      key: 'action',
      render: (text: string, record: any) => (
        <Space size="middle">
          <Button type="primary" onClick={(event) => {
            handleModify('project', record);
          }}></Button>
          <Popconfirm
            title=" ?"
            onConfirm={(e) => confirm(e, record)}
            onCancel={cancel}
            okText=""
            cancelText=""
          >
            <Button type="primary" danger></Button>
          </Popconfirm>
        </Space>
      ),
    },
  ];

  return (
    <>
      <Table size="small" dataSource={dataSource} columns={columns} />
    </>
  );
}

const NoteText = styled.span`
  display: flex;
  width: 120px;
`;
export default ProjectTable;
