import React from 'react';
import styled from 'styled-components';
import { Table, Tag, Space, Button, Popconfirm, message } from 'antd';

function UserTable({ dataSource, handleModify, handleDelete }: any) {
  function confirm(e: any, record: any) {
    console.log(record);
    handleDelete('user', record);
    message.success('  ');
  }
  console.log('UserTable data ', dataSource);
  function cancel(e: any) {
    console.log(e);
    message.error('  ');
  }
  const columns = [
    {
      title: '',
      dataIndex: 'id',
      key: 'id'
    },
    {
      title: ' ',
      dataIndex: 'username',
      key: 'username'
    },
    {
      title: '',
      dataIndex: 'company_name',
      key: 'company_name'
    },
    {
      title: ' ',
      dataIndex: 'manager_name',
      key: 'manager_name',
    },
    {
      title: '',
      dataIndex: 'contact_number',
      key: 'contact_number',
    },
    {
      title: 'E-mail',
      dataIndex: 'email',
      key: 'email',
    },
    {
      title: ' ',
      key: 'projectNameList',
      dataIndex: 'projectNameList',
      render: (projectNameList: string[], record: any) => (
        <>
          {projectNameList?.map(list => {
            const color = list.length > 5 ? 'geekblue' : 'green';
            return (
              <Tag color={color} key={list}>
                {list.toUpperCase()}
              </Tag>
            );
          })}
        </>
      ),
    },
    {
      title: '',
      dataIndex: 'note',
      key: 'note',
      render: (text: string) => {
        return <NoteText>{text}</NoteText>;
      }
    },
    {
      title: 'Action',
      key: 'action',
      render: (text: string, record: any) => (
        <Space size="middle">

          <Button type="primary" onClick={(event) => {
            handleModify('user', record);
          }}></Button>
          <Popconfirm
            title=" ?"
            onConfirm={(e) => confirm(e, record)}
            onCancel={cancel}
            okText=""
            cancelText=""
          >
            <Button type="primary" danger></Button>
          </Popconfirm>
        </Space>
      ),
    },
  ];

  return (
    <Table size="small" dataSource={dataSource} columns={columns} />
  );
}

const NoteText = styled.span`
  display: flex;
  width: 120px;
`;
export default UserTable;
