import React from 'react';
import styled from 'styled-components';
import { Data } from '../../constant/Dashboard';
import ReportList from './ReportList';
import ReportDash from './ReportDash';

function Report(props: { ip: string, data: Data, dummy: Data, lineData: any }) {
  const { data, lineData } = props;
  console.log('  :', data);
  const { dummy } = props;

  return (
    <ReportWrapper>
      <ReportList data={dummy} />
      <ReportDash lineData={lineData} />
    </ReportWrapper>
  );
}
const ReportWrapper = styled.div`
  position: relative;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
`;
export default Report;
