import React, { useEffect } from 'react';
import styled from 'styled-components';
import { Form, Input, Button, Select } from 'antd';
import { setupAxiosInterceptors, apiAddOrUpdateUser } from '../../lib/apiUser';
import { totalmem } from 'os';
//
import { useAppSelector, useAppDispatch } from '../../redux/hooks';
import { addUser, updateUser, openUser, handleUserList } from '../../redux/userSlice';

const { TextArea } = Input;
const { Option } = Select;

const UserForm = (props: any) => {
  const userForm = useAppSelector(state => state.user.form);
  const dispatch = useAppDispatch();
  const [form] = Form.useForm();
  const { formRef, user, modVisible, project, totalProjectList, handleData } = props;

  console.log('UserForm user ', user);

  const totalProjectNameList: any[] = [];

  if (totalProjectList !== undefined) {
    for (let i = 0; i < totalProjectList.length; i++) {
      if (user !== undefined) { //    +     
        if (totalProjectList[i].user_id === user.id) {
          totalProjectNameList.push(<Option key={totalProjectList[i].id} value={totalProjectList[i].name}>{totalProjectList[i].name}</Option>);
        }
      }
      if (totalProjectList[i].user_id === '' || totalProjectList[i].user_id == null) {
        totalProjectNameList.push(<Option key={totalProjectList[i].id} value={totalProjectList[i].name}>{totalProjectList[i].name}</Option>);
      }
    };
  }
  // const onFinish = (values: any) => {
  //   const data = {
  //     id: values.id === undefined ? '' : values.id,
  //     username: values.username === undefined ? '' : values.username,
  //     password: values.password === undefined ? '' : values.password,
  //     company_name: values.company_name === undefined ? '' : values.company_name,
  //     manager_name: values.manager_name === undefined ? '' : values.manager_name,
  //     contact_number: values.contact_number === undefined ? '' : values.contact_number,
  //     email: values.email === undefined ? '' : values.email,
  //     note: values.note === undefined ? '' : values.note,
  //     projectNameList: values.projectNameList,
  //     projects: values.projects,
  //   };
  //   console.log('  : update api');
  //   console.log(props);
  //   console.log('data ', data);

  //   setupAxiosInterceptors();
  //   apiAddOrUpdateUser(data).then(response => {
  //     console.log('apiAddOrUpdateUser ============ ', response.data);
  //   });

  //   formRef.current!.resetFields();
  //   (modVisible === true) ? props.handleModify(data) : props.handleUser(data);
  //   props.handleVisible();
  // };
  const onFinish = (values: any) => {
    const data = {
      id: values.id === undefined ? '' : values.id,
      username: values.username === undefined ? '' : values.username,
      password: values.password === undefined ? '' : values.password,
      company_name: values.company_name === undefined ? '' : values.company_name,
      manager_name: values.manager_name === undefined ? '' : values.manager_name,
      contact_number: values.contact_number === undefined ? '' : values.contact_number,
      email: values.email === undefined ? '' : values.email,
      note: values.note === undefined ? '' : values.note,
      projectNameList: values.projectNameList,
      projects: values.projects,
    };
    /** 
     * update api   
     * setupAxiosInterceptors();
     * apiAddOrUpdateUser(data).then(response => {
     *   console.log('apiAddOrUpdateUser ============ ', response.data);
     * });
     * */
    dispatch(handleUserList(data));
    if (userForm.mode === 'modify') {
      dispatch(updateUser(data));
      dispatch(openUser(false));
    } else {
      formRef.current!.resetFields();
      dispatch(addUser(data));
      dispatch(openUser(false));
    }
  };
  const onFinishFailed = (errorInfo: any) => {
    console.log('Failed:', errorInfo);
  };
  const handleSelectChange = (value: any) => {
    console.log(`Selected: ${value}`);
  };
  useEffect(() => {
    form.setFieldsValue(handleData);
  }, [form, handleData]);
  return (
    <StyledForm>
      <Form
        ref={formRef}
        form={form}
        name="user"
        labelCol={{ span: 10 }}
        wrapperCol={{ span: 16 }}
        onFinish={onFinish}
        onFinishFailed={onFinishFailed}
        autoComplete="off"
      >
        <Form.Item label="id" style={{ display: 'none' }} name="id">
          <Input type="text" />
        </Form.Item>
        <Form.Item label="projects" style={{ display: 'none' }} name="projects" />


        {/* <Form.Item label="id" name="id">
          <Input type="text" />
        </Form.Item>
        <Form.Item label="projects" name="projects">
          <Input type="text" />
        </Form.Item> */}

        <Form.Item
          label={' ID'}
          name="username"
          rules={[{ required: true, message: ' ID ' }]}
        >
          <Input disabled={userForm.mode === 'modify' ? true : false} placeholder=" ID" />
        </Form.Item>
        <Form.Item
          label={' PW'}
          name="password"
          rules={[{ required: true, message: ' PW ' }]}
        >
          <Input placeholder=" PW" />
        </Form.Item>

        <Form.Item
          label={''}
          name="company_name"
          rules={[{ required: true, message: ' ' }]}
        >
          <Input disabled={userForm.mode === 'modify' ? true : false} placeholder="" />
        </Form.Item>
        <Form.Item
          label={''}
          name="manager_name"
          rules={[{ required: true, message: '  ' }]}
        >
          <Input placeholder="" />
        </Form.Item>
        <Form.Item
          label={''}
          name="contact_number"
        >
          <Input type="tel" placeholder="" />
        </Form.Item>
        <Form.Item
          label={'E-mail'}
          name="email"
          rules={[{ type: 'email', message: '   ' }]}
        >
          <Input placeholder="example@email.com" />
        </Form.Item>
        <Form.Item
          label={' '}
          name="projectNameList"
        >
          <Select
            mode="multiple"
            size={'middle'}
            placeholder="Please select"
            onChange={handleSelectChange}
            style={{ width: '100%' }}
            defaultValue={user === undefined ? [] : user.projectNameList}
          >
            {totalProjectNameList}
          </Select>
        </Form.Item>
        <Form.Item
          label={''}
          name="note"
        >
          <TextArea rows={4} />
        </Form.Item>

        <CustomButtonGroup>
          <CustomButton>
            <Button type="primary" htmlType="submit">
              {(userForm.mode === 'modify') ? '' : ''}
            </Button>
          </CustomButton>
          <CustomButton>
            <Button onClick={() => {
              formRef.current!.resetFields();
              dispatch(openUser(false));
            }}>
              
            </Button>
          </CustomButton>
        </CustomButtonGroup>
      </Form>
    </StyledForm>
  );
};

const StyledForm = styled.div`
  padding: 20px;
  width: 100%;
  background-color: ${(props) => props.theme.clusterBg};
  border-radius: 15px;
  & label {
    color: ${(props) => props.theme.fontColor};
  }
`;
const CustomButtonGroup = styled.div`
  display: flex;
  justify-content: space-between;
  width: 150px;
  margin: 0 auto;
`;

const CustomButton = styled.div`
  width: 70px;
  & button {
    width:100%;
  }
`;

export default UserForm;