import React from 'react';
import styled from 'styled-components';
import { Layout, Tabs } from 'antd';
import { UserTab } from './AdminTabs/User';
import { ProjectTab } from './AdminTabs/Project';
import { SectionTab } from './AdminTabs/Section';

const { TabPane } = Tabs;
function callback(key: string) {
  console.log('       ', key);
}

function Admin({ users, projects }: any) {
  const { Content } = Layout;

  return (
    <AdminWrapper>
      <Layout>
        <Content className="site-layout" style={{ padding: '50px', overflow: 'auto' }}>
          <Tabs onChange={callback} type="card" className="custom-tabs" >
            <TabPane tab="" key="1">
              <ProjectTab />
            </TabPane>
            <TabPane tab="" key="2">
              <UserTab />
            </TabPane>
            <TabPane tab="  " key="3">
              <SectionTab />
            </TabPane>
          </Tabs>
        </Content>
      </Layout>
    </AdminWrapper>
  );
}


const AdminWrapper = styled.div`
  position: relative;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;

  & section {
    height: calc(100vh - 60px);
    margin-top: 60px;

    & .custom-tabs {
      background-color: ${(props) => props.theme.clusterBg};
      padding: 10px;
      border-radius: 10px;
    }

    & .page-title {
      font-size: 1.5em;
      font-weight: bold;
    }
  }
`;
export default Admin;
