import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { Breadcrumb } from 'antd';
import AddButton from '../../../components/Common/AddButton';
import AddModal from '../AddModal/AddModal';
import UserTable from '../UserTable';
//
import { useAppSelector, useAppDispatch } from '../../../redux/hooks';
import { setForm, deleteUser, deleteUserList } from '../../../redux/userSlice';

export const UserTab = () => {
  const fetchUser = useAppSelector(state => state.user);
  useEffect(() => {
    console.log(fetchUser, 'changed');
  }, [fetchUser]);
  const { form } = fetchUser;
  const [userData, getData] = useState();
  //    
  //  api     
  const dispatch = useAppDispatch();

  const handleForm = (type: string) => {
    dispatch(setForm({ type, mode: 'add', visible: true }));
  };
  const handleModify = (type: string, record: any) => {
    dispatch(setForm({ type, mode: 'modify', visible: true }));
    getData(record);
    console.log(record);
  };
  const handleDelete = (type: string, record: any) => {
    dispatch(deleteUserList(record.id));
    //  
    dispatch(deleteUser(record));
  };
  return (
    <>
      <AddModal type={form.type} handleData={userData} />
      <TitleWrapper>
        <Breadcrumb className="page-title" style={{ margin: '16px 0' }}>
          <Breadcrumb.Item> </Breadcrumb.Item>
        </Breadcrumb>
        <div>
          <AddButton name={' '} type={'user'} handleForm={handleForm} />
        </div>
      </TitleWrapper>
      <Contents>
        <UserTable dataSource={fetchUser.user} handleDelete={handleDelete} handleModify={handleModify} />
      </Contents>
    </>
  );
};


const Contents = styled.div`
  background: #ffffff;
  padding: 24px;
  min-height: 380px;
  max-width: 100vw;
  border-radius: 15px;
`;

const TitleWrapper = styled.div`
  display: flex;
  justify-content: space-between;
  padding: 0 1em;
`;
